.App {
    text-align: center;
    height: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}

.App-header {
  background-color: #222;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

.App-tree-holder {
    -ms-flex: 1 1;
        flex: 1 1;
}
.rst__toolbarButton:focus, .rst__toolbarButton button:focus {
    outline: none;
}
.rst__toolbarButton button {
    background: none;
    border: 1px solid transparent;
    -webkit-transition: .3s all;
    -o-transition: .3s all;
    transition: .3s all;
    border-radius: 50%;
}

.rst__toolbarButton button:hover {
    border-color: darkgray;
    -webkit-box-shadow: 1px 2px 5px darkgray;
            box-shadow: 1px 2px 5px darkgray;
}

.App-button {
    margin: 10px;
    border-radius: 4px;
    border-width: 1px;
}



